@import url(https://fonts.googleapis.com/css2?family=Syne:wght@700&display=swap);
body {
  background-color:white;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.menu {
    text-align: right;
    
}

#navIcon {
    color: red;
    font-size: 48px;
}

#loginlink{
    color: rgba(242, 240, 237);
}

.menu-area  {
    position: absolute;
    list-style-type: none;
    
    
  }

.menu ul {
    float: right;
    height: 130px;
    width: 100px;
    list-style-type: none;
    background-color: #D2F4F7;
    text-align: left;
    box-shadow: 2px 2px 2px 2px #E6F1F2;
    border-radius: 20px;
    margin:auto;
    padding-top: 5px;
    padding: 0;
    
}

.menu li {
    margin: 0px 10px 15px 15px;
    text-align: left;
}

a {
    color: #4E9DA6;
    font-size: 16px;
}
.square {
    margin-left: auto;
    margin-right: auto;
    height: 30vh;
    width: 30vh;
    background-image:url(/static/media/logo.ecf015ac.jpg);
    background-repeat: no-repeat;
    background-color: #fbf5fb;
    background-position: center;
    display: block;
    box-shadow: 2px 2px 2px 2px #F2E9D8;
    border-radius: 25px;
}
#login {
	background-color: white;
	margin-right: auto;
	margin-left: auto;
	width: 50%;
	-webkit-align-items: center;
	        align-items: center;
}

h1, h2, h3 {
    font-family: 'Alegreya Sans SC', sans-serif;
    font-weight:bolder;
}

h4, h5, h6, p {
    font-family: 'Roboto', sans-serif;
}

#loginForm {
    -webkit-align-items: left;
            align-items: left;
    text-align: left;
    height: 100%;
    background-color: white;
    opacity: 70%;
    border-radius: 20px;
    width: 200px;
    padding: 20px;
   
}

