#loginbtn {
	margin: 20px;
}

#createbtn {
	margin: 20px;
    background-color: #D92B04;
}

input {
	margin: 10px;
}

#logo {
	width: auto;
	height: 70vh;
	margin-right: auto;
	margin-left: auto;
	opacity: 100%;
}

#googlelogin {
	text-align: left;
	font-family: sans-serif;
	-webkit-align-items: center;
	        align-items: center;
	margin-right: auto;
	margin-left: auto;
    
}

i {
    font-size: 48px;
    text-align: center;
    -webkit-align-items: center;
            align-items: center;
}

.rando {
    border-radius: 50%;
    margin-right: auto;
    margin-left: auto;
    height: 300px;
    width: 300px;
    background-color: white;
    background-image: url(/static/media/8.1e6e2017.jpg);
    background-repeat: no-repeat;
    background-position: center;
}
h2 {
    font-family: 'Alegreya Sans SC', sans-serif;
    font-weight:bolder;
}
.home {
    text-align: center;
    height: 100%;
    width: 95%;
}

.home p {
    margin: 0px;
}

.home h3 {
    padding: 0;
}

#found {
    text-align: right;
}

#good {
    text-align: left;
}

#attention {
    color: #D92B04;
    font-size: 1.2em;
    text-decoration: underline;
}

/* #attention:before {
    transition: all 0.2s ease-out;
} */

.imgDiv {
    max-width: 100%;
    height: auto;
}

.detailColumnOne {
    float: left;
    width: 20%;
}

.detailColumnTwo {
    float: left;
    width: 80%;
    letter-spacing: 0.15rem;
}

#resName {
    font-weight: bolder;
    border: 1px black;
}

.resDetails:after {
    content: "";
    display: table;
    clear: both;
}

.resContext {
    margin: auto;
    padding-bottom: 10px;
}

.resInfo {
    -webkit-column-count: 2;
            column-count: 2;
    grid-column-gap: 5px;
    -webkit-column-gap: 5px;
            column-gap: 5px;
    margin-bottom: 10px;
}

.resDetails li {
    list-style: none;
}

#price {
    text-align: left;
    color: #D92B04;
    font-size: 18px;
    font-family: 'Syne', sans-serif;
    letter-spacing: 2px;
}

#distance {
    text-align: right;
    float: left;
    width: 50%;
    letter-spacing: 0;
}

#distance i {
    padding-right: 5px;
    color: #808080;
}

#distance span {
    font-size: 12px;
    color: #808080;
}

#rating {
    color: #F29F05;
    font-size: 15px;
}

#phone,
.label {
    font-size: 13px;
    padding-top: 4px;
}

#phone {
    float: left;
    width: 50%;
    font-size: 12px;
    padding-top: 3px;
}

#nopeBtn {
    margin-left: 5px;
}

hr {
    opacity: 0.5;
}





ul {
    text-align: left;
    list-style-type: none;
}
footer {
    margin-top: 30px;
    text-align: center;
    font-weight: bold;
    background-color: white;
  }
html {
  background-color: white;
  background-repeat: repeat;
  width: 100vw;
  height: 100vh;
}

.container {
  padding-top: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
  padding-bottom: 20px;
  border-radius: 10px;
  background-color: #E6F1F2;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

